import React, { Component } from "react";

class Papers extends Component {
  render() {
    return (
        <section id="skills">
            <div className="col-md-12">
            <div className="col-md-12">
                <h1 className="section-title">
                <span className="text-white">Publications</span>
                </h1>
            </div>
            <div className="col-md-12 text-center">
                <div id="paper">
                    <p >
                    <a href="https://arxiv.org/pdf/2106.15490.pdf">[1]</a> Mvubu, M., Kabuga, E., Plitz, C., Bah, B., Becker, R. and Zimmermann, H.G., 2020, July. On Error Correction Neural Networks for Economic Forecasting. In 2020 IEEE 23rd International Conference on Information Fusion (FUSION) (pp. 1-8). IEEE.
                    </p>
                </div>
                
            </div>
            </div>
      </section>
    );
  }
}

export default Papers;
